import React, { useMemo } from "react";
import PropTypes from 'prop-types'
import { Calendar, dayjsLocalizer, momentLocalizer } from "react-big-calendar";
import { Button, Popover, Select, Space, Typography, message } from "antd";
import moment from "moment/moment";
import "moment-timezone"
import "react-big-calendar/lib/css/react-big-calendar.css";
import "./index.css"

const now = new Date()
const defaultTZ = moment.tz.guess()

const copyText = data => {
  try {
    const dataNode = document.createElement('input');
    dataNode.value = data;
    document.body.appendChild(dataNode);
    dataNode.select();
    dataNode.setSelectionRange(0, dataNode.value.length);
    document.execCommand('Copy');
    document.body.removeChild(dataNode);
    message.success('Copied', 0.5);
  } catch (error) {
    message.error('Copy Faild', 0.5);
  }
}

function Event(props) {
  const {event} = props
  console.log('Event Props: ', props)
  return (
    <div style={{backgroundColor:'white', height: '100%', padding: 5, borderLeft: '5px solid', borderColor: props.event.tag == 'mine'? '#d6e4ff': '#fff1b8'}}>
      <div style={{fontSize: 'small', whiteSpace: 'nowrap', overflow: 'hidden'}}>{moment(props.event.start).format('hh:mm A')} - {moment(props.event.end).format('hh:mm A')}</div>
      <div style={{fontWeight: 'bolder'}}>{event.title}</div>
      <div>{event.desc && ':  ' + event.desc}</div>
      {/* <div>EventNode</div> */}
    </div>
  )
}
Event.propTypes = {
  event: PropTypes.object
}


const CrossCalendar = props => {
  const [view, setView] = React.useState('week')
  const [timezone, setTimezone] = React.useState(defaultTZ)
  const [events, setEvents] = React.useState([
    // {
    //   id: 0,
    //   title: 'Today',
    //   start: new Date(new Date().setHours(new Date().getHours() - 2)),
    //   end: new Date(new Date().setHours(new Date().getHours() + 2)),
    // },
    // {
    //   id: 1,
    //   title: 'Point in Time Event',
    //   start: now,
    //   end: now,
    // },
  ])

  const customComponent = React.useMemo(() => ({
    event: Event,
    eventWrapper: (props) => {
      return (
        <Popover
          placement={view == 'day'? 'bottom': 'right'}
          content={(
            <div style={{width: 320}}>
              <div>{moment(props.event.start).format('hh:mm A')} - {moment(props.event.end).format('hh:mm A')}</div>
              <div style={{fontWeight: 'bolder', fontSize: 'larger'}}>{props.event.title}</div>
              <Space style={{marginTop: 5}}>
                <Button
                  size="small"
                  onClick={() => {
                    copyText(JSON.stringify({data: [props.event]}))
                  }}
                >Copy</Button>
                <Button 
                  size='small' 
                  danger
                  onClick={() => {
                    setEvents(prev => [...prev.filter(elem => elem.id != props.event.id)])
                  }}
                >Delete</Button>
              </Space>
            </div>
          )} 
          trigger="click"
        >
          {props.children}
        </Popover>
      )
    },
    // eventContainerWrapper: props => {
    //   return (
    //     <div>
    //       {props.children}
    //       <div>EventContainerWrapper</div>
    //     </div>
    //   )
    // }    
  }), [view])

  const eventPropGetter = React.useCallback(
    (event, start, end, isSelected) => {
      console.log('eventPropGetter: ', event)
      return {
        style: {
          color: 'gray',
          border: '1px solid #d0d0d0',
          backgroundColor: 'white',
          padding: 0
        }
      }
    }
  , [])

  const formats = React.useMemo(() => ({
    // eventtitle
    eventTimeRangeFormat: ({ start, end }, culture, localizer) => {
      //return moment(start).format('hh:mm A') + ' - ' + moment(end).format('hh:mm A')
      return ''
    }
  }), [])

  const handleSelectSlot = React.useCallback(
    ({ start, end }) => {
      const title = window.prompt('New Event Name')
      if (title) {
        setEvents(prev => [...prev, {id:prev.length , start, end, title, tag: 'mine' }])
      }
    },
    [setEvents]
  )

  const handleImportEvents = React.useCallback(
    e => {
      const importEvents = window.prompt('Paste Calendars Here')
      if(importEvents) {
        try {
          const formatEvents = JSON.parse(importEvents).data.map(elem => {
            elem.start = moment(elem.start).toDate()
            elem.end = moment(elem.end).toDate()
            return elem
          })
          console.log('formatEvents: ', formatEvents)
          setEvents(prev => [...prev, ...formatEvents.map((elem, index) => {
            elem.id = prev.length + index
            elem.tag = 'others'
            return elem
          })])
        } catch (error) {
          message.error('Import Events: ' + error, 1)
        }
      }
    },
    []
  )

  const handleSelectEvent = React.useCallback(
    (event) => {
      //window.alert(event.title)
      console.log('handleSelectEvent: ', event)
    },
    []
  )

  const { localizer } = useMemo(() => {
    moment.tz.setDefault(timezone)
    return {
      localizer: momentLocalizer(moment)
    }
  }, [timezone])

  React.useEffect(() => {
    //console.log('tz: ', moment.tz.names())
  }, [])

  return (
    <div style={{margin: 10}}>
      <Calendar
        view={view}
        onView={v => setView(v)}
        dayLayoutAlgorithm="no-overlap"
        components={customComponent}
        localizer={localizer}
        events={events}
        eventPropGetter={eventPropGetter}
        startAccessor="start"
        endAccessor="end"
        style={{ height: '85vh' }}
        formats={formats}
        selectable
        onSelectEvent={handleSelectEvent}
        onSelectSlot={handleSelectSlot}
      />
      <div style={{display: 'flex', justifyContent: 'space-evenly', margin: 10}}>
        <Space>
          <div>Local: {moment.tz.guess()}</div>
          <Select
            showSearch
            value={timezone}
            filterOption={(input, option) =>
              (option?.label ?? '').toLowerCase().includes(input.toLowerCase())
            }
            options={moment.tz.names().map(elem => {
              return {
                label: elem,
                value: elem
              }
            })}
            style={{width: 200}}
            onChange={e => {
              console.log('onTimezoneChange: ', e)
              setTimezone(e)
            }}
          />
          <Button
            onClick={() => setTimezone(defaultTZ)}
          >Reset</Button>
        </Space>
        <Space>
          <Button onClick={handleImportEvents}>
            Import Calendar
          </Button>
          <Button
            onClick={() => {
              copyText(JSON.stringify({data: events}))
            }}
          >Copy Calendar</Button>
        </Space>
      </div>
      <Space>
        <Typography.Text type='secondary'>
          Cross Calendar  A shareable calendar designed for cross-border teams.
        </Typography.Text>

        <Typography.Text type='secondary'>
          Contact: xuyoung@whu.edu.cn
        </Typography.Text>

        <Typography.Link href="/">
          Home Page
        </Typography.Link>
      </Space>
      {/* <div style={{height: 500}}>
        <Typography.Paragraph>
          { 
            events.map((elem, index) => {
              return (
                <blockquote key={index}>
                  {JSON.stringify(elem)}
                </blockquote>
              )
            })
          }
        </Typography.Paragraph>
      </div> */}
    </div>
  )
}

export default CrossCalendar;