import logo from './logo.svg';
import './App.css';
import { Routes, Route } from 'react-router-dom';
import Home from './home'
import CrossCalendar from './cross-calendar';

function App() {
  return (
    <div className="App">
      <div >
        <Routes>
          <Route path='' Component={Home} />
          <Route path='/' Component={Home} />
          <Route path='/cross-calendar' Component={CrossCalendar}/>
        </Routes>
      </div>
    </div>
  );
}

export default App;
